.library{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 20rem;
    background: white;
    box-shadow: 2px 2x 50px rgb(200, 200, 200);
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    h2{
        padding: 2rem;
    }
}
.library-song{
    display: flex;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    cursor: pointer;
    transition: background 0.5s ease;
    img{
        width: 30%;
    }
    &:hover{
        background: rgb(222, 222, 255);
    }
    .song-description{
        padding-left: 1rem;
        h3{
            font-size: 1rem;
        }
        h4{
            font-size: 0.7rem;
        }
    }
}

*{
    scrollbar-width:thin;
    scrollbar-color: rgba(155,155,155,0.5) transparent;
}
*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar-track{
    background: transparent;
}
*::-webkit-scrollbar-thumb{
    background-color: rgb(155,155,155);
    border-radius: 20px;
    border: transparent;
}
.selected{
    background:rgb(165, 181, 228);
}
.active-library{
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width:768px) {
    .library{
        width: 100%;
    }
}